<template>
    <div class="list-header-title">
        <div class="item-box" v-if="menuList && menuList.hasOwnProperty('labelList')">
            <div class="item" v-for="item in menuList.labelList" @click="gotoRouter(item)">{{ item.text }}</div>
        </div>
        <div class="item-btn" v-if="menuList && menuList.hasOwnProperty('buttonList')">
            <el-button type="primary" class="item" size="mini" v-for="(item,index) in menuList.buttonList" :key="index" @click="gotoRouter(item)">{{ item.text }}</el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "listHeaderTitle",
        props:{
            menuList:{
                type:Object,
                default:()=>{
                    return {};
                }
            },
        },
        methods:{
            //点击
            gotoRouter(item) {
                if (item && item.hasOwnProperty('router')) {
                    let params = {};
                    if (item.hasOwnProperty('params')) {
                        Object.assign(params, item.params);
                    }
                    if (item.hasOwnProperty('paramsData')) {
                        Object.assign(params, item.paramsData);
                    }
                    this.$router.push({name: item.router, params: params});
                } else if (item && typeof item.func === "function") {
                    if (item.hasOwnProperty('funcData')) {
                        item.func(item.funcData);
                    } else {
                        item.func();
                    }
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/common/lib.scss";
    .list-header-title{
        position: relative;
        width: 100%;
        height: 50px;
        line-height: 50px;
        background-color: #DCDFE6;
        @extend .no-select;
        .item-box{
            position: relative;
            width: 100%;
            height: 100%;
            line-height: inherit;
            white-space: nowrap;
            z-index: 90;
            .item{
                position: relative;
                display: inline-block;
                height: 100%;
                padding: 0 20px;
                box-sizing: border-box;
                white-space: normal;
                word-wrap: break-word;
                word-break: break-all;
                @include diy-animation-show(all);
                cursor: pointer;
                color: #606266;
                font-size: 14px;

                &:hover{
                    background-color: #F2F6FC;
                }

                &:first-child{
                    color: #303133;
                    background-color: #ffffff;
                }
            }
        }

        .item-btn{
            position: absolute;
            top: 0;
            right: 5px;
            height: 100%;
            line-height: inherit;
            white-space: nowrap;
            z-index: 100;

            .item{
                position: relative;
                display: inline-block;
                box-sizing: border-box;
                margin-right: 10px;
            }
        }
    }
</style>