<template>
    <div class="distributorList">
        <div class="main-table" v-if="distributorList">
            <el-table
                    v-loading="loading"
                    :data="distributorList"
                    :empty-text="$L('暂无数据')"
                    :header-cell-style="{padding:'10px',color:'#666'}"
                    style="width: 100%" class="table">
                <el-table-column
                        prop="grade"
                        :label="$L('等级')"
                        align="center">
                </el-table-column>
                <el-table-column
                        :label="$L('用户')"
                        align="center">
                    <div slot-scope="scope">
                        <p>{{ scope.row.contact ? scope.row.contact : scope.row.userName }}{{scope.row.mobile?'/'+scope.row.mobile:''}}</p>
                    </div>
                </el-table-column>
                <el-table-column
                        prop="subordinateCount"
                        :label="$L('下级分销商数')"
                        align="center">
                </el-table-column>
                <el-table-column
                        prop="memberCount"
                        :label="$L('子会员数')"
                        align="center">
                </el-table-column>
                <el-table-column
                        prop="totalAmount"
                        :label="$L('订单总金额')"
                        align="center">
                </el-table-column>
                <el-table-column
                        prop="fullCommission"
                        :label="$L('产生佣金总额')"
                        align="center">
                </el-table-column>
                <el-table-column
                        :label="$L('状态')"
                        align="center">
                    <div slot-scope="scope" class="status">
                        <span :class="statusFunc(scope.row.status).color">{{ statusFunc(scope.row.status).text }}</span>
                    </div>
                </el-table-column>
                <el-table-column
                        :label="$L('操作')"
                        min-width="120"
                        align="center">
                    <div slot-scope="scope" class="button">
                        <a class="btns" :href="scope.row.fxUrl" target="_blank">
                            <el-button size="small">{{ $L('进入平台') }}</el-button>
                        </a>
                        <router-link :to="{name:'distributorDetails',params:{id:scope.row.distributorId,type:'all'}}">
                            <el-button type="primary" size="small">{{ $L('查看详情') }}</el-button>
                        </router-link>
                    </div>
                </el-table-column>
            </el-table>
        </div>
		<div class="bottom" v-if="distributorList.length > 0">
			<router-link :to="{name:'distributorList'}" class="mainFontColor">
				{{$L('查看更多')}}
			</router-link>
		</div>
    </div>
</template>

<script>
    export default {
        name: "distributorList",
        props:{
            distributorList:{
                type:Array,
                default:() => {
                    return [];
                }
            }
        },
        data() {
            return {
                loading: null
            }
        },
        methods:{
            statusFunc(value){
                let x;
                switch(value){
                    case -1:
                        x = {text:this.$L('停用'),color:'font-red'};
                        break;
                    default:
                        x = {text:this.$L('生效'),color:'font-green'};
                        break;
                }
                return x;
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/common/lib";
    .distributorList{
        /deep/ .main-table{
            background: #fff;
            .is-leaf{
                background-color: #fff;
            }
            .el-table__header-wrapper .cell{
                font-weight: 800;
            }
            .table{
                .status{
                    .font-red {
                        color: #ff0000;
                    }
                    .font-green {
                        color: #00bf00;
                    }
                }
                .button{
                    .btns{
                        margin-right: 10px;
                    }
                    .btns:last-child{
                        margin-right: 0;
                    }

                }
				.el-table__empty-block {
				    @extend .no-select;
				    position: relative;
				    width: 100%;
				    height: 380px;
				    line-height: 380px;
				    text-align: center;
				    background-color: #ffffff;
				    font-size: 16px;
				    color: #999;
				    margin-bottom: 20px;
				}
            }
            
        }
		.bottom{
			height: 70px;
			line-height: 70px;
			background-color: #FFFFFF;
			text-align: center;
			font-size: 14px;

            .mainFontColor{
                @include diy-animation-show(opacity);
                &:hover{
                    opacity: 0.8;
                }
            }
		}
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>