<template>
    <div class="home" v-loading="loading">
        <sectionTitle v-bind:tit="$L('我的个人中心')"></sectionTitle>
        <div class="user-info">
            <userCard :account-type="baseInfo && baseInfo.hasOwnProperty('accountInfo') ? baseInfo.accountInfo.accountType:''" :card-info="baseInfo && baseInfo.hasOwnProperty('userInfo') ? baseInfo.userInfo : {}"></userCard>
            <user-detail :data-arr="dataArr"></user-detail>
        </div>
        <distributorInfo :distributor-arr="distributorArr" v-if="baseInfo.hasOwnProperty('accountInfo') && baseInfo.accountInfo.accountType === 3"></distributorInfo>
        <template v-if="baseInfo.hasOwnProperty('accountInfo') && baseInfo.accountInfo.accountType !== 2 ">
            <sectionTitle :tit="titleArr"></sectionTitle>
            <list-header-title class="list-header-title" :menu-list="listHeader"></list-header-title>
        </template>
        <order-list :link="'link'" :order-list="baseInfo && baseInfo.hasOwnProperty('paymentOrderList') ? baseInfo.paymentOrderList : []" v-if="baseInfo.hasOwnProperty('accountInfo') && baseInfo.accountInfo.accountType === 1 "></order-list>
        <distributor-list :distributor-list="baseInfo && baseInfo.hasOwnProperty('distributionList') ? baseInfo.distributionList : []" v-else-if="baseInfo.hasOwnProperty('accountInfo') && baseInfo.accountInfo.accountType === 3"></distributor-list>
        <reservation-list :reservation-list="baseInfo && baseInfo.hasOwnProperty('reservationList') ? baseInfo.reservationList : []" v-else-if="baseInfo.hasOwnProperty('accountInfo') && baseInfo.accountInfo.accountType === 4"></reservation-list>
    </div>
</template>

<script>

    import sectionTitle from "../../components/common/sectionTitle.vue"
    import userCard from "../../components/home/userCard.vue"
    import userDetail from "../../components/home/userDetail.vue"
    import orderList from "../../components/order/orderList.vue"
    import distributorInfo from "../../components/home/distributorInfo"
    import listHeaderTitle from "../../components/common/listHeaderTitle"
    import distributorList from "../../components/home/distributorList.vue"
    import reservationList from "../../components/home/reservationList.vue"

    export default {
        name: "home",
        components: {
            listHeaderTitle,
            distributorInfo,
            userCard, sectionTitle, userDetail, orderList,distributorList,
            reservationList
        },
        data() {
            return {
                loading: true,
                baseInfo: {},
            }
        },
        computed: {
            dataArr: {
                get() {
                    let re = [];
                    if (this.baseInfo.hasOwnProperty('accountInfo')) {
                        if (this.baseInfo.accountInfo.accountType === 2){
                            //展示版
                            if (this.baseInfo.accountInfo.hasOwnProperty('message')) {
                                re.push({
                                    icon: 'bbx-font bbx-icon-weiduxinxi',
                                    title: this.$L('未读信息'),
                                    value: this.baseInfo.accountInfo.message,
                                    router: 'messageList',
                                });
                            }
                            if (this.baseInfo.accountInfo.hasOwnProperty('message')) {
                                re.push({
                                    icon: 'bbx-font bbx-icon-liuyanban',
                                    title: this.$L('我的留言'),
                                    value: this.baseInfo.accountInfo.leaveCount,
                                    router: 'leaveList',
                                });
                            }
                            if (this.baseInfo.accountInfo.hasOwnProperty('message')) {
                                re.push({
                                    icon: 'bbx-font bbx-icon-xinxifabu',
                                    title: this.$L('信息发布'),
                                    value: this.baseInfo.accountInfo.informationCount,
                                    router: 'information',
                                });
                            }
                            if (this.baseInfo.accountInfo.hasOwnProperty('address')) {
                                re.push({
                                    icon: 'bbx-font bbx-icon-shouhuodizhi',
                                    title: this.$L('我的地址'),
                                    value: this.baseInfo.accountInfo.address,
                                    router: 'addressList'
                                });
                            }
                            if (this.baseInfo.accountInfo.hasOwnProperty('message')) {
                                re.push({
                                    icon: 'bbx-font bbx-icon-weibiaoti-2',
                                    title: this.$L('账户管理'),
                                    value: '',
                                    router: 'accountSafety',
                                });
                            }
                        } else {
                            if (this.baseInfo.accountInfo.hasOwnProperty('balance')) {
                                re.push({
                                    icon: 'bbx-font bbx-icon-weibiaoti--29',
                                    title: this.$L('余额'),
                                    value: this.baseInfo.accountInfo.balance,
                                    router: 'payRecord',
                                    link: {
                                        text: this.$L('充值'),
                                        router: 'payCharge',
                                    }
                                });
                            }
                            if (this.baseInfo.accountInfo.hasOwnProperty('integral')) {
                                re.push({
                                    icon: 'bbx-font bbx-icon-wodejifen',
                                    title: this.$L('积分'),
                                    value: this.baseInfo.accountInfo.integral,
                                    router: 'pointList'
                                });
                            }
                            if (this.baseInfo.accountInfo.hasOwnProperty('coupon')) {
                                re.push({
                                    icon: 'bbx-font bbx-icon-wodeqiaquan',
                                    title: this.$L('优惠券'),
                                    value: this.baseInfo.accountInfo.coupon,
                                    router: 'couponList',
                                    params:{
                                        type:'canUse'
                                    }
                                });
                            }
                            if (this.baseInfo.accountInfo.hasOwnProperty('message')) {
                                re.push({
                                    icon: 'bbx-font bbx-icon-weiduxinxi',
                                    title: this.$L('未读信息'),
                                    value: this.baseInfo.accountInfo.message,
                                    router: 'messageList',
                                });
                            }
                            if (this.baseInfo.accountInfo.hasOwnProperty('accountType') && this.baseInfo.accountInfo.accountType === 4) {
                                if (this.baseInfo.accountInfo.hasOwnProperty('reservationOrder')) {
                                    re.push({
                                        icon: 'bbx-font bbx-icon-wodeyuyue',
                                        title: this.$L('预约订单'),
                                        value: this.baseInfo.accountInfo.reservationOrder,
                                        router: 'reservation',
                                        params: {
                                            type: 'all'
                                        }
                                    });
                                }
                            } else {
                                if (this.baseInfo.accountInfo.hasOwnProperty('deliveryOrder')) {
                                    re.push({
                                        icon: 'bbx-font bbx-icon-daishouhuo',
                                        title: this.$L('待收货'),
                                        value: this.baseInfo.accountInfo.deliveryOrder,
                                        router: 'orderList',
                                        params: {
                                            type: 'needReceipt'
                                        }
                                    });
                                }
                            }
                            if (this.baseInfo.accountInfo.hasOwnProperty('accountType') && this.baseInfo.accountInfo.accountType === 4) {
                                if (this.baseInfo.accountInfo.hasOwnProperty('reservationOrder')) {
                                    re.push({
                                        icon: 'bbx-font bbx-icon-wodeyuyue',
                                        title: this.$L('预约待付款'),
                                        value: this.baseInfo.accountInfo.paymentOrder,
                                        router: 'reservation',
                                        params: {
                                            type: 'payment'
                                        }
                                    });
                                }
                            } else {
                                if (this.baseInfo.accountInfo.hasOwnProperty('paymentOrder')) {
                                    re.push({
                                        icon: 'bbx-font bbx-icon-daifukuan',
                                        title: this.$L('待付款'),
                                        value: this.baseInfo.accountInfo.paymentOrder,
                                        router: 'orderList',
                                        params: {
                                            type: 'needPay'
                                        }
                                    });
                                }
                            }
                        }
                    }
                    return re;
                }
            },
            listHeader: {
                get() {
                    let re = {};
                    if (this.baseInfo.hasOwnProperty('accountInfo')) {
                        if (this.baseInfo.accountInfo.hasOwnProperty('accountType')) {
                            switch (this.baseInfo.accountInfo.accountType) {
                                case 1:
                                case 2:
                                    //商城展示版
                                    re = {
                                        labelList: [
                                            {
                                                text: this.$L('待付款'),
                                                router: 'orderList',
                                                params: {
                                                    type: 'needPay'
                                                }
                                            },
                                            {
                                                text: this.$L('待收货'),
                                                router: 'orderList',
                                                params: {
                                                    type: 'needReceipt'
                                                }
                                            },
                                            {
                                                text: this.$L('已完成'),
                                                router: 'orderList',
                                                params: {
                                                    type: 'finish'
                                                }
                                            },
                                            {
                                                text: this.$L('已关闭'),
                                                router: 'orderList',
                                                params: {
                                                    type: 'cancel'
                                                }
                                            },
                                            {
                                                text: this.$L('退款/售后'),
                                                router: 'orderList',
                                                params: {
                                                    type: 'afterSale'
                                                }
                                            },
                                        ]
                                    };
                                    break;
                                case 3:
                                    //分销版
                                    re = {
                                        labelList: [
                                            {
                                                text:this.$L('我的分销商'),
                                                router: 'distributorList',
                                            },
                                            {
                                                text:this.$L('分销订单'),
                                                router:'distributorOrder',
                                                paramsData: {
                                                    type: 'all',
                                                },
                                            },
                                        ],
                                        buttonList:[
                                            {
                                                text: this.$L('申请提现'),
                                                router: 'applyWithdrawal'
                                            }
                                        ]
                                    };
                                    break;
                                case 4:
                                    //预约版
                                    re = {
                                        labelList: [
                                            {
                                                text: this.$L('待付款'),
                                                router: 'reservation',
                                                params: {
                                                    type: 'payment'
                                                }
                                            },
                                            {
                                                text: this.$L('预约成功'),
                                                router: 'reservation',
                                                params: {
                                                    type: 'success'
                                                }
                                            },
                                            {
                                                text: this.$L('已完成'),
                                                router: 'reservation',
                                                params: {
                                                    type: 'finish'
                                                }
                                            },
                                        ]
                                    };
                                    break;
                                case 5:
                                    //代理独立版
                                    re = {
                                        labelList: [
                                            {
                                                text:'我的代理商',
                                                router: 'agentMain',
                                            },
                                            {
                                                text:'代理订单',
                                                router:'agentOrder',
                                            },
                                            {
                                                text:'团队排名',
                                                router:'agentRanking',
                                            },
                                        ],
                                        buttonList:[
                                            {
                                                text: this.$L('申请提现'),
                                                router: 'applyAgentWithdrawal'
                                            }
                                        ]
                                    };
                                    break;
                            }
                        }
                    }
                    return re;
                }
            },
            titleArr:{
              get(){
                  let re;
                  if (this.baseInfo.hasOwnProperty('accountInfo')) {
                      if (this.baseInfo.accountInfo.hasOwnProperty('accountType')) {
                          switch (this.baseInfo.accountInfo.accountType){
                              case 1:
                              case 2:
                                  re = this.$L('我的订单');
                                  break;
                              case 3:
                                  re = this.$L('我的分销');
                                  break;
                              case 4:
                                  re = this.$L('预约订单');
                                  break;
                              case 5:
                                  re = this.$L('我的代理');
                                  break;
                          }
                      }
                  }
                  return re;
              }
            },
            distributorArr:{
                get(){
                    let re = [];
                    if (this.baseInfo.hasOwnProperty('accountInfo')) {
                        if(this.baseInfo.accountInfo.hasOwnProperty('distributionTotal')){
                            re.push({
                                icon: 'bbx-font bbx-icon-weibiaoti--5',
                                title: this.$L('分销订单总金额'),
                                value: this.baseInfo.accountInfo.distributionTotal,
                                router: 'distributorOrder',
                                params: {
                                    type: 'all'
                                }
                            });
                        }
                        if(this.baseInfo.accountInfo.hasOwnProperty('distributionCommission')){
                            re.push({
                                icon: 'bbx-font bbx-icon-weibiaoti--6',
                                title: this.$L('分销总佣金'),
                                value: this.baseInfo.accountInfo.distributionCommission,
                                router: 'distributorWithdrawal',
                            });
                        }
                        if(this.baseInfo.accountInfo.hasOwnProperty('distributionWithdraw')){
                            re.push({
                                icon: 'bbx-font bbx-icon-weibiaoti--4',
                                title: this.$L('可提现佣金'),
                                value: this.baseInfo.accountInfo.distributionWithdraw,
                                router: 'distributorWithdrawal',
                            });
                        }
                        re.push({
                            icon: 'bbx-font bbx-icon-dianputuiguang',
                            title: this.$L('店铺推广'),
                            value: this.$L('推广设置'),
                            router: 'distributorSettings',
                        });
                    }
                    return re;
                }
            }
        },
        created() {
            this.getData();
        },
        methods: {
            getData() {
                const _self = this;
                _self.loading = true;
                _self.$api('indexBaseInfo', {}, function (reData) {
                    if (reData.flag === 1) {
                        _self.baseInfo = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$gotoWebHome();
                    });
                });
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/common/lib.scss";

    .home {
        position: relative;
        width: 100%;
        padding: 20px;

        .user-info {
            position: relative;
            width: 100%;
            margin: 10px 0 20px;
            height: 230px;
        }

        .tab-head {
            margin-top: 10px;
        }

        .list-header-title {
            margin-top: 10px;
        }
    }

</style>