<template>
    <div class="user-card mainBgColor">
        <div class="card-detail">
            <div class="card-logo" @click="gotoRouter('accountInfo')">
                <user-avatars class="img" :src="userImg"></user-avatars>
            </div>
            <div class="card-info">
                <p class="shop-name" @click="gotoRouter('accountInfo')">{{ userName }}</p>
                <p v-if="hasUserCard == 1" class="user-level" :class="{'open-card':openUserCard}" @click="gotoRouter(openUserCard ? 'userCard' : '')">
                    <i class="bbx-font bbx-icon-huiyuan1"></i>
                    <span class="name-item">{{ userGroup }}</span>
                </p>
                <p class="name" v-if="cardInfo.agentGroup">
                    <i class="bbx-font bbx-icon-mingcheng"></i>
                    <span class="name-item">{{ cardInfo.agentGroup ? cardInfo.agentGroup : ''}}</span>
                </p>
            </div>
        </div>
        <template v-if="accountType !== 2">
            <div class="card-progress" :class="{'open-card':openUserCard}" @click="gotoRouter('userCard')">
                <span v-if="hasUserCard == 0"> &nbsp; </span>
                <span v-else class="tips" >{{ $L('经验值') }}{{ nowEXP }}
                <span v-if="isAssign === 1"> </span>
                <span v-else>
                    <i v-if="nexEXP == 0">
                        {{ $L('已达到最高级') }}
                    </i>
                    <i v-else-if="nexEXP !== 0">
                        ,{{ $L('距离下一级还差') }}{{ nexEXP }}{{ $L('经验值') }}
                    </i>

                </span>
            </span>
                <el-progress :percentage="nowEXP<=0 ? 0 : nowEXP/(nowEXP+nexEXP)*100" color="#00CDF9" :show-text="false"
                             :stroke-width="2"></el-progress>
                <p class="exp">
                    <span class="span-left">EXP {{ nowEXP }}</span>
                    <span  v-if="hasUserCard == 1" class="span-right">
                    <i>
                        {{ nexEXP === 0 ? '' :   $L('EXP') + Number(nowEXP+nexEXP)  }}
                    </i>
                </span>

                </p>
            </div>
        </template>

        <div class="vipCard" @click="$router.push({'name':'userCard'})" v-if="openUserCard">
            <p class="p" v-if="hasUserCard">{{ $L('我的会员卡')}}</p>
            <p class="p" v-else>{{ $L('领取会员卡')}}</p>
        </div>
    </div>
</template>

<script>
    import UserAvatars from "../common/userAvatars";
    export default {
        name: "userCard",
        components: {UserAvatars},
        props: {
            cardInfo: {
                type: Object,
                default: () => {
                    return {};
                }
            },
            accountType:{
                type: Number
            }
        },
        computed: {
            userImg: {
                get() {
                    if (this.cardInfo && this.cardInfo.hasOwnProperty('userImg')) {
                        return this.cardInfo.userImg;
                    } else {
                        return '';
                    }
                }
            },
            userName: {
                get() {
                    if (this.cardInfo && this.cardInfo.hasOwnProperty('userName')) {
                        return this.cardInfo.userName;
                    } else {
                        return this.$L('您好！');
                    }
                }
            },
            openUserCard: {
                get() {
                    if (this.cardInfo && this.cardInfo.hasOwnProperty('openUserCard') && this.cardInfo.openUserCard === 1) {
                        return true;
                    } else {
                        return false;
                    }
                }
            },
            hasUserCard:{
                get() {
                    if (this.cardInfo && this.cardInfo.hasOwnProperty('hasUserCard') && this.cardInfo.hasUserCard === 1) {
                        return true;
                    } else {
                        return false;
                    }
                }
            },
            isAssign : {
                get() {
                    if (this.cardInfo && this.cardInfo.hasOwnProperty('isAssign')) {
                        return true;
                    } else {
                        return false;
                    }
                }
            },
            userGroup: {
                get() {
                    if (this.cardInfo && this.cardInfo.hasOwnProperty('userGroup')) {
                        return this.cardInfo.userGroup;
                    } else {
                        return '';
                    }
                }
            },
            cardDesc: {
                get() {
                    return this.$L('经验值{$1}，距离下一级还差{$2}经验值', [this.nowEXP, this.nexEXP])
                }
            },
            nowEXP: {
                get() {
                    let nowEXP = 0;
                    if (this.cardInfo && this.cardInfo.hasOwnProperty('nowEXP')) {
                        nowEXP = this.cardInfo.nowEXP;
                    }
                    return nowEXP;
                }
            },
            nexEXP: {
                get() {
                    let nexEXP = 0;
                    if (this.cardInfo && this.cardInfo.hasOwnProperty('nexEXP')) {
                        nexEXP = this.cardInfo.nexEXP;
                    }
                    return nexEXP;
                }
            }
        },
        methods: {
            gotoRouter(name) {
                this.$router.push({name: name});
            }
        },
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/common/common";
    @import "../../assets/css/common/lib";

    .user-card {
        position: absolute;
        width: 400px;
        height: 100%;
        padding: 30px 20px 0;
        color: #fff;
        overflow: hidden;
        top: 0;
        left: 0;
        z-index: 100;
        @extend .no-select;

        .card-detail {
            overflow: hidden;

            .card-logo {
                float: left;
                overflow: hidden;
                width: 100px;
                height: 100px;
                line-height: 100px;
                box-sizing: border-box;
                border-radius: 50%;
                padding: 10px;
                border: 1px solid #fff;
                color: #ffffff;
                text-align: center;
                @include diy-animation-show(all);
                cursor: pointer;

                .img {
                    width: 100%;
                    height: 100%;
                    border-radius: inherit;
                    line-height: inherit;

                    .img-error {
                        width: 100%;
                        height: 100%;
                    }
                }
                /deep/ .img-error {
                    width: 100%;
                    height: 100%;
                    border-radius: 50%;
                }
                /deep/ .el-image__inner{
                    border-radius: 50%;
                }

                &:hover {
                    opacity: 0.8;
                }
            }

            .card-info {
                float: left;
                padding-left: 15px;
                padding-top: 5px;
                width: calc(100% - 120px);
                .shop-name {
                    @include diy-animation-show(all);
                    cursor: pointer;
                    font-size: 20px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    width: 80%;
                    &:hover {
                        opacity: 0.8;
                    }
                }
                .name-item{
                    height: 30px;
                    line-height: 30px;
                    float: left;
                }
                .user-level {
                    height: 30px;
                    line-height: 30px;
                    .bbx-font {
                        color: #FCD23D;
                        font-size: 26px;
                        margin-right: 10px;
                        float: left;
                    }
                    &.open-card{
                        @include diy-animation-show(all);
                        cursor: pointer;
                        &:hover {
                            opacity: 0.8;
                        }
                    }
                }
                .name{
                    height: 30px;
                    line-height: 30px;
                    .bbx-font {
                        font-size: 26px;
                        margin-right: 10px;
                        float: left;
                    }
                }
            }
        }

        .card-progress {
            margin-top: 25px;
            .el-progress {
                margin: 5px 0;
            }
            .tips{
                width: 100%;
                display: inline-block;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }
            .exp {
                .span-left {
                    float: left;
                }

                .span-right {
                    float: right;
                }
            }
            &.open-card{
                cursor: pointer;
                @include diy-animation-show(opacity);

                &:hover{
                    opacity: 0.8;
                }
            }
            .num-name{
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }
        }

        .vipCard {
            background: url("../../assets/img/home/vipCard.png") no-repeat left bottom;
            width: 133px;
            height: 117px;
            position: absolute;
            top: -15px;
            right: -15px;
            @include diy-animation-show;
            cursor: pointer;

            .p {
                color: #FF6000;
                position: absolute;
                top: 60px;
                left: 40px;
                font-weight: 700;
                transform: rotate(38deg);
                -ms-transform: rotate(38deg);
                -moz-transform: rotate(38deg);
                -webkit-transform: rotate(38deg);
                -o-transform: rotate(38deg);
            }

            &:hover {
                top: 0;
                right: 0;
                @include diy-animation-show;
            }
        }
    }
</style>
