<template>
    <div class="user-detail">
        <div class="data-items mainBorderColorHover" v-for="item in dataArr">
            <div class="items-box" @click="gotoRouter(item)">
                <div class="icon-box">
                    <i class="icon mainFontColor" v-bind:class="item.icon"></i>
                </div>
                <div class="info">
                    <div class="title">
                        <div class="txt">{{item.title}}</div>
                    </div>
                    <div class="value">
                        <div class="txt">{{item.value }}</div>
                    </div>
                </div>
            </div>
            <div v-if="item.hasOwnProperty('link')" class="link mainFontColor" @click="gotoRouter(item.link)">{{item.link.text}}</div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "userDetail",
        props: {
            dataArr: {
                type: Array,
                default: () => {
                    return [];
                }
            }
        },
        methods:{
            //点击
            gotoRouter(item) {
                if (item && item.hasOwnProperty('router')) {
                    let params = {};
                    if (item.hasOwnProperty('params')) {
                        Object.assign(params, item.params);
                    }
                    if (item.hasOwnProperty('paramsData')) {
                        Object.assign(params, item.paramsData);
                    }
                    this.$router.push({name: item.router, params: params});
                } else if (item && typeof item.func === "function") {
                    if (item.hasOwnProperty('funcData')) {
                        item.func(item.funcData);
                    } else {
                        item.func();
                    }
                } else if (item && item.hasOwnProperty('params') || item && item.hasOwnProperty('paramsData')) {
                    let params = {};
                    if (item.hasOwnProperty('params')) {
                        Object.assign(params, item.params);
                    }
                    if (item.hasOwnProperty('paramsData')) {
                        Object.assign(params, item.paramsData);
                    }
                    this.$router.push({params: params});
                }
            }
        }
    }

</script>

<style lang="scss" scoped>
    @import "../../assets/css/common/lib";

    .user-detail {
        position: absolute;
        left: 420px;
        top: 0;
        z-index: 90;
        width: calc(100% - 420px);
        height: 100%;

        .data-items {
            background: #fff;
            margin-bottom: 20px;
            width: calc((100% - 40px) / 3);
            height: 105px;
            float: left;
            margin-right: 20px;
            text-align: center;
            position: relative;
            @include diy-animation-show;
            cursor: pointer;
            border: 1px solid #eff1f5;
            @extend .no-select;

            &:nth-child(3n) {
                margin-right: 0;
            }
            .items-box{
                position: relative;
                overflow: hidden;
                width: 100%;
                height: 100%;
                .icon-box {
                    position: absolute;
                    width: 105px;
                    height: 100%;
                    top: 0;
                    left: 0;
                    z-index: 100;
                    line-height: 105px;
                    text-align: center;

                    .icon {
                        font-size: 35px;
                    }
                }

                .info {
                    position: absolute;
                    width: calc(100% - 105px);
                    height: 100%;
                    top: 0;
                    left: 105px;
                    z-index: 90;
                    text-align: left;

                    .title {
                        position: relative;
                        width: 100%;
                        height: 50%;
                        font-size: 14px;
                        color: #999;

                        .txt {
                            position: absolute;
                            line-height: 100%;
                            bottom: 10px;
                            left: 0;
                        }
                    }

                    .value {
                        position: relative;
                        width: 100%;
                        height: 50%;
                        font-size: 20px;
                        color: #666;

                        .txt {
                            position: absolute;
                            line-height: 100%;
                            top: 0;
                            left: 0;
                        }
                    }
                }
            }

            .link {
                position: absolute;
                line-height: 100%;
                bottom: calc(50% + 10px);
                right: 20px;
                z-index: 100;
                font-size: 14px;
                @include diy-animation-show;

                &:hover{
                    opacity: 0.8;
                }
            }
        }
    }
</style>