<template>
    <div class="reservation-list">
        <div class="reservation-content" v-loading="loading" v-if=" reservationList.length > 0">
            <div class="list-main" v-for="(item,key) in reservationList" :key="key">
                <div class="list-msg">
                    <span class="inner-span">{{$L('订单编号：')}}{{ item.orderSn }}</span>
                    <span class="order-time">{{$L('下单时间：')}}{{ item.addTime }}</span>
                </div>
                <div class="list-head">
                    <div class="item item-two">{{$L('预约订单')}}</div>
                    <div class="item item-one">{{$L('价格')}}</div>
                    <div class="item item-one">{{$L('预付金额')}}</div>
                    <div class="item item-one">{{$L('预约日期')}}</div>
                    <div class="item item-one">{{$L('订单状态')}}</div>
                    <div class="item item-three">{{$L('操作')}}</div>
                </div>
                <div class="list-info">
                    <div class="item item-two">
                        <div class="info">
                            <div class="list-main-left">
                                <img :src="item.productPic" alt="">
                            </div>
                            <div class="list-main-right">
                                <p class="order-info">{{item.proName}}</p>
                            </div>
                        </div>
                    </div>
                    <div class="item item-one">
                        <div class="price font-grey">{{ item.orderAmount }}</div>
                    </div>
                    <div class="item item-one">
                        <div class="price font-red">{{ item.moneyPaid }}</div>
                    </div>
                    <div class="item item-one">
                        <div class="font">
                            {{ item.bookTime }}<br>
                            <span v-if="item.timeSpec">{{ item.timeSpec }}</span>
                        </div>
                    </div>
                    <div class="item item-one">
                        <div v-if="item.order_status == 1" class="font font-red">{{$L('待处理')}}</div>
                        <div v-else-if="item.order_status == 2" class="font">{{$L('已消费')}}</div>
                        <div v-else-if="item.order_status == 3" class="font">{{$L('已取消预约')}}</div>
                        <div v-else-if="item.order_status == 4 && item.comments_status === 0" class="font">{{$L('待评价')}}</div>
                        <div v-else-if="item.order_status == 4 && item.comments_status === 1" class="font">{{$L('已评价')}}</div>
                        <div v-else-if="item.order_status == 5" class="font">{{$L('已逾期')}}</div>
                        <div v-else class="font">{{$L('未知状态')}}</div>
                        <el-button type="text" @click="getInfoFunc(item.id)">{{$L('订单详情')}}</el-button>
                    </div>
                    <div class="item item-three">
                        <el-button type="primary" v-if="item.order_status === 1 && item.pay_status === 0" @click="changeOrderType(item,'pay')">{{$L('立即支付')}}</el-button>
                        <el-button type="default" v-if="item.order_status === 1"  @click="changeOrderType(item,'cancel')">{{$L('取消预约')}}</el-button>
                        <el-button type="default" v-else-if="item.order_status >= 3"  @click="changeOrderType(item,'del')">{{$L('删除订单')}}</el-button>
                        <el-button type="primary" v-if="item.order_status >= 3 && item.comments_status === 0"  @click="changeOrderType(item,'evaluate')">{{$L('去评价')}}</el-button>
                        <el-button type="primary" v-if="item.order_status >= 3 && item.comments_status === 1"  @click="changeOrderType(item,'evaluateInfo')">{{$L('查看评价')}}</el-button>
                    </div>
                </div>
            </div>
			<div class="bottom" v-if="reservationList.length > 0">
				<router-link :to="{name:'reservation',params:{type:'all'}}" class="mainFontColor">
					{{$L('查看更多')}}
				</router-link>
			</div>
        </div>
        <div class="tips" v-else v-loading="loading">
            <p class="no-list">{{ $L('无预约信息') }}</p>
        </div>
    </div>
</template>

<script>
    export default {
        name: "reservationList",
        props:{
            reservationList: {
                type: Array,
                default: () => {
                    return [];
                }
            }
        },
        data(){
            return {
                loading:null
            }
        },
        methods:{
            changeOrderType(item, status) {
                switch (status) {
                    case 'pay':
                        //立即付款
                        if(item.order_status === 0 || item.pay_status === 1){
                            this.$store.commit('setDialogObj', {
                                name: this.$L('订单支付'),
                                view: orderPay,
                                width: '900px',
                                loading: true
                            });
                        }
                        break;
                    case 'cancel':
                        //取消订单
                        if (item.order_status === 1) {
                            this.$confirm(this.$L('确认取消订单吗？'), this.$L('取消订单'), {
                                confirmButtonText: this.$L('立即取消'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('cancleReservation', {
                                    orderId: item.orderId,
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        this.getData();
                                    } else {
                                        console.log(reData);
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    console.log(error);
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'del':
                        //删除订单
                        if (item.order_status >= 3) {
                            this.$confirm(this.$L('是否确认删除订单？'), this.$L('删除订单'), {
                                confirmButtonText: this.$L('立即删除'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('delReservation', {
                                    orderId: item.orderId,
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        this.getData();
                                    } else {
                                        console.log(reData);
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    console.log(error);
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'evaluate':
                        //去评价
                        if (item.order_status >= 3 && item.comments_status === 0) {
                            this.$router.push({name: "resOrderComment", params: {id: item.orderId}});
                        }
                        break;
                    case 'evaluateInfo':
                        //去评价
                        if (item.order_status >= 3 && item.comments_status === 1) {
                            this.$router.push({name: "resOrderComment", params: {id: item.orderId}});
                        }
                        break;
                }
            },
            getInfoFunc(id){
                this.$router.push({name:'reservationInfo',params:{id:id}});
            },
        }
    }
</script>

<style lang="scss" scoped>
	@import "../../assets/css/common/lib";
    .reservation-list{
        .reservation-content{
            .list-main{
                background-color: #fff;
                padding: 15px 0;
                margin-bottom: 15px;
                .list-msg{
                    background: #fff;
                    font-size: 14px;
                    .inner-span{
                        padding: 0 40px 0 20px;
                        height: 50px;
                        line-height: 50px;
                    }
                    .order-time{
                        color: #999;
                    }
                }
                .list-head{
                    display: flex;
                    margin: 0 20px;
                    border: 1px solid #dcdfe6;
                    .item{
                        height: 50px;
                        line-height: 50px;
                        font-size: 14px;
                        text-align: center;
                        background: #fff;
                        color: #666;
                        font-weight: 700;
                        padding: 0 20px;
                        &.item-one{
                            width: 13%;
                        }
                        &.item-two{
                            width: 35%;
                            text-align: left;
                        }
                        &.item-three{
                            font-size: 14px;
                            width: 13%;
                        }
                    }
                }
                .list-info{
                    margin: 0 20px;
                    background: #fff;
                    padding: 20px 0;
                    .item {
                        display: inline-block;
                        text-align: center;
                        vertical-align: top;
                        &.item-one{
                            width: 13%;
                            height: 100px;
                            font-size: 14px;
                            .price{
                                height: 100px;
                                margin-bottom: 20px;
                                padding-top: 10px;
                                &:last-child{
                                    margin-bottom: 0;
                                    font-weight: 700;
                                }
                                .font-grey{
                                    color: #999;
                                    text-decoration: line-through;
                                }
                            }
                            .font-red{
                                color: red;
                                padding: 10px 0;
                            }
                            .font{
                                padding: 10px 0;
                            }
                            .unconfirm{
                                color: green;
                                padding: 10px 0;
                            }
                            .undeliver{
                                color: red;
                                padding: 10px 0;
                            }
                        }
                        &.item-two{
                            width: 35%;
                            text-align: left;
                        }
                        &.item-three{
                            width: 13%;
                            font-size: 14px;
                            padding: 10px 0;
                            button{
                                display: block;
                                margin: 0 auto 10px;
                                display: block;
                                width: 100px;
                                line-height: 35px;
                                padding: 0;
                                &:last-child{
                                    margin-bottom: 0;
                                }
                            }
                        }
                        .info{
                            position: relative;
                            width: 100%;
                            overflow: hidden;
                            padding-bottom: 20px;
                            &:last-child{
                                padding-bottom: 0;
                            }
                            .list-main-left{
                                width: 100px;
                                height: 100px;
                                float: left;
                                img{
                                    width: 100px;
                                    height: 100px;
                                }
                            }
                            .list-main-right{
                                padding-left: 18px;
                                float: left;
                                height: 100px;
                                width: calc(100% - 120px);
                                p{
                                    line-height: 26px;
                                }
                                .order-info{
                                    cursor: pointer;
                                    overflow:hidden;
                                    display:-webkit-box;
                                    text-overflow:ellipsis;
                                    -webkit-line-clamp:2;
                                    -webkit-box-orient:vertical;
                                    white-space:normal;
                                    font-size: 14px;
                                }
                            }
                        }
                    }
                }
            }
			.bottom{
				height: 70px;
				line-height: 70px;
				background-color: #FFFFFF;
				text-align: center;
				font-size: 14px;
			}
        }
        .tips{
            position: relative;
            background: #fefefe;
            padding: 20px;
            text-align: center;
            .no-list{
                @extend .no-select;
				position: relative;
				width: 100%;
				height: 380px;
				line-height: 380px;
				text-align: center;
				background-color: #ffffff;
				font-size: 16px;
				color: #999;
				margin-bottom: 20px;
            }
        }
    }
</style>