<template>
    <div class="distributorInfo" v-if="distributorArr">
        <div class="data-items mainBorderColorHover" v-for="item in distributorArr">
            <div class="items-box" @click="gotoRouter(item)">
                <div class="icon-box">
                    <i class="icon mainFontColor" v-bind:class="item.icon"></i>
                </div>
                <div class="info">
                    <div class="title">
                        <div class="txt">{{item.title}}</div>
                    </div>
                    <div class="value">
                        <div class="txt">{{item.value}}</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "distributorInfo",
        props:{
            distributorArr:{
                type:Array,
                default: () => {
                    return [];
                }
            }
        },
        data(){
            return {

            }
        },
        methods:{
            //点击
            gotoRouter(item) {
                if (item && item.hasOwnProperty('router')) {
                    let params = {};
                    if (item.hasOwnProperty('params')) {
                        Object.assign(params, item.params);
                    }
                    if (item.hasOwnProperty('paramsData')) {
                        Object.assign(params, item.paramsData);
                    }
                    this.$router.push({name: item.router, params: params});
                } else if (item && typeof item.func === "function") {
                    if (item.hasOwnProperty('funcData')) {
                        item.func(item.funcData);
                    } else {
                        item.func();
                    }
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/common/lib";

    .distributorInfo{
        position: relative;
        width: 100%;
        overflow: hidden;
        margin-bottom: 15px;
        .data-items {
            background: #fff;
            width: calc(100% / 4);
            height: 105px;
            float: left;
            text-align: center;
            position: relative;
            @include diy-animation-show;
            cursor: pointer;
            border: 1px solid #fff;
            @extend .no-select;

            &:nth-child(3n) {
                margin-right: 0;
            }

            .items-box{
                position: relative;
                overflow: hidden;
                width: 100%;
                height: 100%;

                .icon-box {
                    position: absolute;
                    width: 105px;
                    height: 100%;
                    top: 0;
                    left: 0;
                    z-index: 100;
                    line-height: 105px;
                    text-align: center;

                    .icon {
                        font-size: 35px;
                    }
                }

                .info {
                    position: absolute;
                    width: calc(100% - 105px);
                    height: 100%;
                    top: 0;
                    left: 105px;
                    z-index: 90;
                    text-align: left;

                    .title {
                        position: relative;
                        width: 100%;
                        height: 50%;
                        font-size: 14px;
                        color: #999;

                        .txt {
                            position: absolute;
                            line-height: 100%;
                            bottom: 10px;
                            left: 0;
                        }
                    }

                    .value {
                        position: relative;
                        width: 100%;
                        height: 50%;
                        font-size: 20px;
                        color: #666;

                        .txt {
                            position: absolute;
                            line-height: 100%;
                            top: 0;
                            left: 0;
                        }
                    }
                }
            }
            &:last-child{
                .value {
                    font-size: 18px!important;
                }
            }
        }
    }
</style>